	module.exports = {"pathContext":{"docHtml":"<h1 id=\"argo-cd-sshknownhosts-file-customization\"><a href=\"#argo-cd-sshknownhosts-file-customization\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Argo CD ssh<em>known</em>hosts file customization</h1>\n<p>The directory contains sample kustomize application which customizes <code>/etc/ssh/ssh_known_hosts</code> file in Argo CD. This is useful if you want to disable SSL cert validation\nfor Git repositories connected using SSL urls:</p>\n<ul>\n<li><code>argocd-known-hosts-mounts.yaml</code> - define merge patches which inject <code>/etc/ssh/ssh_known_hosts</code> file mount into all Argo CD deployments.</li>\n<li><code>argocd-known-hosts.yaml</code> - defines <code>ConfigMap</code> which includes <code>/etc/ssh/ssh_known_hosts</code> file content.</li>\n<li><code>kustomization.yaml</code> - Kustomize application which bundles stable version of Argo CD and apply <code>argocd-known-hosts-mounts.yaml</code> patches on top.</li>\n</ul>\n<p>!!! note\nThe <code>/etc/ssh/ssh_known_hosts</code> should include Git host on each Argo CD deployment as well as on a computer where <code>argocd repo add</code> is executed. After resolving issue\n<a href=\"https://github.com/argoproj/argo-cd/issues/1514\">#1514</a> only <code>argocd-repo-server</code> deployment has to be customized.</p>","docPath":"argo-cd/examples/known-hosts/readme","proj":"argo-cd"}}

